<template>
  <div class="components-container">
    <!-- <div class="info"></div> -->
    
    <h3 class="title"></h3>
    <div class="main">
        <div class="box">
                    <p></p>
                    <div class="content">
                        <el-upload
                        class="upload-demo"
                        action="/api/upload/"
                        :on-success="handleSuccess"
                        :on-remove="handleRemove"
                        :file-list="fileList"
                        :before-upload="beforeUpload"
                        list-type="picture"
                        :limit='1'>
                        <el-button size="small" type="primary"></el-button>
                        <div slot="tip" class="el-upload__tip">jpg/jpeg/png500kb</div>
                    </el-upload>
                    </div>
                
            </div>

          <div class="box">
            <p></p>
            <el-input type="text" placeholder="" v-model="title" :maxlength=30></el-input>
          </div>

          

          <div class="box">
              <p></p>
              <el-date-picker
                v-model="date"
                type="datetime"
                placeholder=""
               	>
              </el-date-picker>
          </div>
  
          <div class="editor-container">
            <p></p>
            <UE :defaultMsg=defaultMsg :config=config :id=ue1 ref="ue"></UE>
            <!-- <UE :defaultMsg=defaultMsg :config=config :id=ue2 ref="ue2"></UE> -->
          </div>
           <el-button type="success" class="pull-right" @click="submit()" style="margin-bottom:30px;"></el-button>
    </div>
    
    
  </div>
</template>
<style scoped lang='less'>
.info {
  border-radius: 10px;
  line-height: 20px;
  padding: 10px;
  margin: 10px;
  background-color: #ffffff;
}
.editor-container {
  margin-bottom: 20px;
}
.main{
  padding-left: 40px;
  padding-bottom: 40px;
}
.box {
  width: 500px;
  margin: 10px 0 20px;
}

</style>
<script>
import UE from "components/ue/ue.vue";
import { newsAdd } from 'api/news';
import { delete_pic } from 'api/works';
import { Message } from 'element-ui'

export default {
  components: { UE },
  data() {
    return {
      fileList: [], //
      defaultMsg: "",
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350,
      },
      ue1: "ue1", // id
      title: "", //
      content: "", //
      date: new Date() ,//
      pic_name:"", //
      url:"" //
    };
  },
  methods: {
    beforeUpload(file){
       let size = file.size;
       let type = file.type;
       if((size/1024)>500){
            Message.error({
                    message: ""
            });
            return false
       }

       if(type != 'image/jpeg' && type != 'image/jpg' && type != 'image/png'){
           Message.error({
                    message: "jpgjpegpng"
            });
            return false
       }
    },    
    submit() {
      let content = this.$refs.ue.getUEContent(); // 
      let plainText =  this.$refs.ue.getContentTxt(); //
  
      newsAdd({
        title:this.title
        ,date:this.date,
        content,plainText,
        pic_name:this.pic_name,
        url:this.url
        });
    },
    handleSuccess(data) {
        this.url = data;
        this.pic_name = data.replace('/api/static/img/','');
    },
    handleRemove() {
        //
        delete_pic({pic_name:this.pic_name});
        this.url = "";
        this.pic_name = "";
    },
  }
};
</script>






